.Loader {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    z-index: 50;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ffffff !important;
}

.Loader h1 {
    position: relative;
    top: -100px;
    color: gray;
}